<div class="upload"><div class="subtitle">Contributor Information:</div><div class="submit"><div class="info-panel" v-if="user.ghAuthenticated"> <div class="info-element"><label for="user-info-username">GitHub ID:</label><code id="user-info-username">{{user.username}}</code></div><div class="info-element"><label for="user-info-name">Name:</label><input id="user-info-name" type="text" v-model="user.name"></div><div class="info-element"><label for="user-info-email">Email:</label><input id="user-info-email" type="text" v-model="user.email"></div></div><button id="upload-submit" @click="ghAuthenticate" v-else>Login with GitHub</button></div><div class="subtitle">Upload dataset file:</div><div class="advanced-upload" v-if="advancedUpload"><div class="file-picker"><div class="input"><label for="upload">Dataset:</label><input id="upload" type="file" accept=".pbtxt,.pb" v-on:change="(e) =&gt; setFile(e)"></div></div><div class="copy">Choose a &nbsp;<code>.pbtxt</code>&nbsp; file compiled with the &nbsp;<code>construct_dataset.py</code>&nbsp; script in the &nbsp;<a href="https://github.com/Center-for-Mechanical-Control-of-Chem/cmccdb-schema">cmccdb-schema repository</a></div></div><div class="basic-upload" v-else><div class="file-picker"><div class="input"><label for="upload">Dataset:</label><input id="upload" type="file" accept=".xlsx,.csv" v-on:change="(e) =&gt; setFile(e)"></div></div><div class="copy">Choose a &nbsp;<code>.xlsx/.csv</code>&nbsp; file following the CMCCDB template, examples are in the &nbsp;<a href="https://github.com/Center-for-Mechanical-Control-of-Chem/cmccdb-data">cmccdb-data repository</a></div></div><div class="submit"><button id="upload-submit" @click="submitUpload">Submit Upload</button><input class="advanced-toggle" id="advanced-toggle" type="checkbox" v-model="advancedUpload"><label for="advanced-toggle">Use precompiled dataset</label></div><div class="error-message"><code> <pre>{{ traceback }}</pre></code></div></div>