<div class="enumerate"><div class="subtitle">Upload files for enumeration:</div><div class="file-picker"><div class="input"><label for="template">Template filename:</label><input id="template" type="file" accept=".pbtxt" v-on:change="(e) =&gt; setFile(e,&quot;template&quot;)"></div><div class="input"><label for="spreadsheet">Spreadsheet filename:</label><input id="spreadsheet" type="file" accept=".csv,.xls,.xlsx" v-on:change="(e) =&gt; setFile(e,&quot;spreadsheet&quot;)"></div></div><div class="submit"><button id="enumerate_submit" @click="submitEnumerate">Submit Enumeration Upload</button></div><div class="copy">You can use a Reaction template to enumerate a Dataset based on a spreadsheet of values. For more information, see the <a href="https://docs.open-reaction-database.org/en/latest/guides/templates.html" target="_blank">documentation</a>.</div><div class="copy"><b>NOTE:</b> Large dataset enumerations (thousands of reactions) may result in a browser timeout. If this happens, please send an email to <a href="mailto:help@open-reaction-database.org" target="_blank">help@open-reaction-database.org</a> and attach your template and spreadsheet files. Alternatively, you may use the <a href="https://github.com/Open-Reaction-Database/ord-schema/blob/main/ord_schema/scripts/enumerate_dataset.py" target="_blank">programmatic interface</a> to enumerate the dataset locally.</div></div>